$gold: #c3a159;

.mint-page {

    position: absolute;

    overflow: hidden;

    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    animation: emerge 1s ease-in-out;
}

// @keyframes emerge {
