//     }
//     100%{
//       opacity: 1;
//     }
// }

.mint-page-bg {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100vw;
    height: calc(100vw * 0.5625);
    opacity: 0.6;
    min-height: 100vh;

    img {
        position: absolute;
        height: 100%;
        right: 0;
    }
}

.mint-page-inner {
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 380px;
    height: fit-content;

    animation: slide-to-top 1s ease-in-out;
    max-width: 90vw;
}

@keyframes slide-to-top {
    0% {
        top: 150vh;
    }

    100% {
        top: 0;
    }
}

.mint-page-nft-video {
    position: relative;
    width: 100%;
    aspect-ratio: 1;
    border-radius: 10px;
    box-shadow: 0px 0px 5px 1px rgb(207, 207, 207);
    overflow: hidden;

    video {
        width: 100%;

    }

}

.mint-page-mint-wrapper {
    width: 100%;
}

.mint-page-mint-amount-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 63px;
    width: 100%;
    // background-color: red;
    border-radius: 6px;
    // border: 1px solid rgb(213, 212, 212);
    padding: 0 8px 0 7px;
    box-sizing: border-box;
    max-width: 90vw;

    margin: 1rem 0 1rem;
    box-shadow: 0px 0px 5px 1px rgb(207, 207, 207);

    background-color: rgba(0, 0, 0, 0.4);

    span {
        color: white;
    }

}

.mint-page-mint-amount-btn {
    height: 47px;
    width: 47px;
    color: white;
    cursor: pointer;
    font-size: 1.9rem;
    line-height: 45px;

    border-radius: 6px;
    background-color: #2d2d2d;
    transition: background 200ms ease-in-out, color 200ms ease-in-out;

    &:hover {
        background: white;
        color: black;
    }
}


.mint-page-mint-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 63px;
    width: 100%;
    border-radius: 5px;
    // border: 1px solid rgb(213, 212, 212);
    max-width: 90vw;
    box-shadow: 0px 0px 5px 1px rgb(207, 207, 207);
    background: rgba(0, 0, 0, 0.5);
    transition: background 200ms ease-in-out, color 200ms ease-in-out;

    &:hover {
        // background: linear-gradient(to right, #BF953F,#FCFEBA,#B38728,#FCFEBA,#BF953F)
        background: white;

        span {
            color: black;
        }
    }

}

.loading-button {
    &:hover {
        background-color: rgba(0, 0, 0, 0.5);

        span {
            color: white !important
        }

    }
}