.backstage{
    animation: emerge 1s ease-in-out  ;
    
}
.backstage-page-inner{
    position: absolute;
    bottom:0;
    top:0;
    left:0;
    right:0;
    height: fit-content;

    max-width: 90vw;
    margin: auto;
    text-align: center;
}
